@import url(https://fonts.googleapis.com/css2?family=Gemunu+Libre:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box;margin:0;padding:0;font-family:"Trebuchet MS","Lucida Sans Unicode","Lucida Grande","Lucida Sans",Arial,sans-serif}body{background:#4b79a1;background:linear-gradient(to top, #283E51, #4B79A1);color:#f2f2f2}body .intro-text{font-family:"Gemunu Libre",sans-serif;font-size:3.5rem;text-align:center;margin-bottom:10px}.main-cover{min-height:100vh;padding-bottom:8px}.todo-form{margin-bottom:32px;display:flex;justify-content:center}.todo-form .textfield{background-color:#fff;color:#020202;width:55%}.todo-form .textfield:nth-child(2){margin-left:6px;margin-right:3px}.todo-app{display:flex;flex-direction:column;justify-content:start;width:520px;min-height:600px;background:#161a2b;text-align:center;margin:128px auto;border-radius:10px;padding-bottom:32px}.complete{text-decoration:line-through;opacity:.4}.todo-input{padding:14px 32px 14px 16px;border-top:0px transparent;border-bottom:1px solid #f2f2f2;border-left:0px transparent;border-right:0px transparent;outline:none;width:320px;background:transparent;color:#fff}.todo-input::-webkit-input-placeholder{color:#e2e2e2}.todo-input:-ms-input-placeholder{color:#e2e2e2}.todo-input::placeholder{color:#e2e2e2}.todo-container{display:flex;flex-direction:row;position:relative}.todo-row{display:flex;justify-content:space-between;align-items:center;margin:4px auto;color:#fff;background:linear-gradient(90deg, #ff7614 0%, #ff5411 100%);border-radius:5px;width:60%}.todo-text{width:100%;display:flex;justify-content:space-between;padding:16px;cursor:pointer}.todo-row:nth-child(4n+1){background:linear-gradient(90deg, #5d0cff 0%, #9b00fa 100%)}.todo-row:nth-child(4n+2){background:linear-gradient(90deg, #ff0cf1 0%, #fa0087 100%)}.todo-row:nth-child(4n+3){background:linear-gradient(90deg, #149fff 0%, #117aff 100%)}.icons{display:flex;align-items:center;margin-left:3px;padding-right:12px}.delete-icon{margin-right:5px;color:#fff;cursor:pointer}.edit-icon{color:#fff;cursor:pointer}@media screen and (max-width: 800px){.todo-form{display:flex;flex-direction:column;align-items:center}.todo-form .textfield{margin:0 !important;margin-bottom:10px !important;width:90%}.todo-row{width:90% !important}}
